/* General styles */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
}

.app {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.app.light {
  background-color: #f0f0f0;
  color: #333;
}

.app.dark {
  background-image: url('./dark.jpg');
  background-size: cover;    /* Ensure the entire image is visible */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Avoid repeating the image */
  color: #fff;
}


.app header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #4CAF50, #388E3C);
  color: #fff;
  transition: background 0.3s ease;
  animation: gradient-animation 5s infinite;
}

.app.light header {
  background: linear-gradient(135deg, #6DD5FA, #2980B9);
  animation: gradient-animation-light 5s infinite;
}

.app header .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.app header h1 {
  margin: 0;
  font-size: 24px;
  transition: color 0.3s ease;
}

.app header button {
  padding: 10px 20px;
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
}

.app header button:hover {
  background-color: #fff;
  color: #4CAF50;
  border-color: #4CAF50;
}

.app.light header button:hover {
  color: #6DD5FA;
  border-color: #6DD5FA;
}

.app main {
  flex: 1; /* This ensures the main section takes up remaining space */
  padding: 20px;
}

.app footer {
  padding: 20px;
  text-align: center;
  background-color: #4CAF50; /* Optional: add background color to footer */
  color: #fff; /* Optional: add text color to footer */
  flex-shrink: 0; /* Ensure footer does not shrink */
  background: linear-gradient(135deg, #1a2227, #124e929a);
  background-size: 200% 200%;
  animation: gradient-animation 5s infinite;
}

.app.light footer {
  background: linear-gradient(100deg, #68bbda, #09304b);
  background-size: 200% 200%;
  animation: gradient-animation-light 5s infinite;
}

/* GPAcalculator.css */
.gpa-calculator {
  padding: 20px;
}

.course-list {
  margin-bottom: 20px;
}

.add-course-button,
.calculate-gpa-button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #4CAF50;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-course-button:hover,
.calculate-gpa-button:hover {
  background-color: #45a049;
}

.results {
  text-align: center;
}

.family-reaction {
  font-size: 24px;
  margin-top: 10px;
}

.progress-container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

.progress {
  margin-top: 20px;
}

.course-row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
}

.course-row input[type="text"],
.course-row input[type="number"],
.course-row select {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-right: 10px;
}

.course-row select {
  width: auto; /* Ensures select doesn't take more space than needed */
}

.add-course-button,
.calculate-gpa-button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #4CAF50;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-course-button:hover,
.calculate-gpa-button:hover {
  background-color: #45a049;
}

.results {
  text-align: center;
}

.remarks-heading {
  margin-top: 20px;
  font-size: 24px;
  color: inherit; /* Ensures color adapts to theme */
}

.chart-heading {
  margin-top: 20px;
  font-size: 24px;
  color: inherit; /* Ensures color adapts to theme */
}

.progress-container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .gpa-calculator {
    padding: 10px;
    flex-direction: column;
  }

  .left-pane, .right-pane {
    flex: 100%;
    padding: 10px;
  }

  .course-row {
    flex-direction: row; /* Changed to ensure they stay in the same row */
    align-items: stretch;
  }

  .course-row input[type="text"],
  .course-row input[type="number"],
  .course-row select {
    flex: 1;
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .add-course-button,
  .calculate-gpa-button {
    padding: 8px;
  }

  .remarks-heading {
    font-size: 20px;
  }

  .chart-heading {
    font-size: 20px;
  }
}

/* Gradient Animation for Dark Mode */
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient-animation-light {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.app header {
  background: linear-gradient(135deg, #1a2227, #124e929a);
  background-size: 200% 200%;
  animation: gradient-animation 5s infinite;
}

.app.light header {
  background: linear-gradient(100deg, #68bbda, #09304b);
  background-size: 200% 200%;
  animation: gradient-animation-light 5s infinite;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensures that the app takes up at least the full height of the viewport */
}

.app footer {
  position: fixed; /* Fixed positioning */
  bottom: 0; /* Position the footer at the bottom of the viewport */
  width: 100%; /* Full width */
  padding: 20px;
  text-align: center;
  background-color: #4CAF50; /* Optional: add background color to footer */
  color: #fff; /* Optional: add text color to footer */
  flex-shrink: 0; /* Ensure footer does not shrink */
  background: linear-gradient(135deg, #1a2227, #124e929a);
  background-size: 200% 200%;
  animation: gradient-animation 5s infinite;
}

.app.light footer {
  background: linear-gradient(100deg, #68bbda, #09304b);
  background-size: 200% 200%;
  animation: gradient-animation-light 5s infinite;
}
