/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f7f7f7;
}

.gpa-calculator {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}

.left-pane {
  flex: 70%;
  padding: 20px;
  box-sizing: border-box;
}

.right-pane {
  flex: 30%;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  border-left: 2px solid #ccc; /* Added border to right pane */
}

.theme-toggle {
  cursor: pointer;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #4CAF50;
  color: #fff;
  border: none;
  border-radius: 4px;
  text-align: center;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.theme-toggle:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.course-list {
  margin-bottom: 20px;
}

.course-row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
}

.course-row input[type="text"],
.course-row input[type="number"],
.course-row select {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-right: 10px;
  transition: border-color 0.3s ease;
}

.course-row input[type="text"]:focus,
.course-row input[type="number"]:focus,
.course-row select:focus {
  border-color: #4CAF50;
}

.remove-course-button {
  padding: 10px;
  background-color: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.remove-course-button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
}
.add-course-button,
.calculate-gpa-button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background: linear-gradient(90deg, #102650, #2b4d8d);
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
  animation: gradient-animation 5s infinite;
}

.add-course-button:hover,
.calculate-gpa-button:hover {
  background: linear-gradient(135deg, #102650, #1344a0);
  transform: scale(0); /* Reduced scale effect */
}

.results {
  text-align: center;
}

.remarks-heading {
  margin-top: 20px;
  font-size: 24px;
  color: inherit; /* Ensures color adapts to theme */
}

.chart-heading {
  margin-top: 20px;
  font-size: 24px;
  color: inherit; /* Ensures color adapts to theme */
}

.progress-container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .gpa-calculator {
    padding: 10px;
    flex-direction: column;
  }

  .left-pane, .right-pane {
    flex: 100%;
    padding: 10px;
  }

  .course-row {
    flex-direction: column;
    align-items: stretch;
  }

  .course-row input[type="text"],
  .course-row input[type="number"],
  .course-row select {
    flex: 1;
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .add-course-button,
  .calculate-gpa-button {
    padding: 8px;
  }

  .remarks-heading {
    font-size: 20px;
  }

  .chart-heading {
    font-size: 20px;
  }
}

/* Gradient Animation */
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
